<script>
/* eslint-disable */
</script>

<template>
    <div>
        <div class="common-editor">
            <textarea ref="mycode" v-model="value" ></textarea>
        </div>
        <div v-on:click="run" style='width:100%'><el-button style='width:calc(100% - 0px)'>真实模拟（点击按钮或F2运行代码）</el-button></div>
        <el-dialog
                :fullscreen="true"
                title="真实模拟（运行结果）"
                :visible.sync="dialogVisible"
                width="80%"
                height="100%"><!--:before-close="handleClose"-->
            <div width="100%" height="100%">
            <!-- <span v-html="popret"></span> -->
            <json-viewer
                    :value="popret"
                    :expand-depth=5
                    copyable
                    boxed
                    sort></json-viewer>
            </div>
            <!-- <iframe src="https://www.xionggd.com" frameborder="0" width="100%" height="600px"></iframe> -->
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="dialogVisible = false">确 定</el-button>
            </span>
        </el-dialog>
    </div>
</template>
<script>
import 'codemirror/lib/codemirror.css'
import 'codemirror/theme/base16-dark.css'
import 'codemirror/theme/paraiso-light.css'
import 'codemirror/theme/blackboard.css'
import 'codemirror/addon/hint/show-hint.css'
import 'codemirror/addon/hint/show-hint.js'
import 'codemirror/addon/hint/show-hint'
import 'codemirror/addon/hint/javascript-hint'
import '../../../public/poplang-hint'
// import 'codemirror/addon/hint/sql-hint'
import 'codemirror/mode/javascript/javascript'
// import 'codemirror/mode/markdown/markdown'
// import 'codemirror/mode/sql/sql'
// import 'codemirror/mode/php/php'
// import 'codemirror/mode/python/python'
// import 'codemirror/mode/shell/shell'
// import 'codemirror/mode/powershell/powershell'
import { string } from 'mathjs'
import { deepCopy } from '@/utils/utils'
// import protocol from '../../utils/dnalink_protocol_config'
 
const CodeMirror = require('codemirror/lib/codemirror')
import Vue from 'vue'
import JsonViewer from 'vue-json-viewer'
Vue.use(JsonViewer)

export default {
    name: 'CodeEditor',
    props: {
        // 这个value绑定了CommonAttr里面的
        value: {
            type: String,
            default: '',
        },
        language: {
            type: String,
            default: null,
        },
        element: {
            type: Object,
            default: () => {},
        },
    },
    data() {
        return {
            dialogVisible:false,
            popret:'',
            CommonEditor: false,
            code: '',
            coder: null,
            mode: 'poplang',
            theme: 'default',
            // readOnly:true,
            modes: [
                { value: 'javascript', label: 'Javascript' },
                // { value: 'x-java', label: 'Java' },
                // { value: 'x-python', label: 'Python' },
                // { value: 'x-sql', label: 'SQL' },
                // { value: 'x-shell', label: 'Shell' },
                // { value: 'x-powershell', label: 'PowerShell' },
                // { value: 'x-php', label: 'PHP' },
            ]
        }
    },
    watch: {
        language: {
            handler(language) {
                this.getCoder().then(() => {
                // 尝试从父容器获取语法类型
                    if (language) {
                    // 获取具体的语法类型对象
                        const modeObj = this.getLanguage(language)
                        // 判断父容器传入的语法是否被支持
                        if (modeObj) {
                            this.mode = modeObj.label
                            this.coder.setOption('mode', `text/${modeObj.value}`)
                        }
                    }
                })
            },
            immediate: true,
        },
    },
    computed: {
        coderOptions() {
            let This = this;
            return {
                line: true,
                mode: 'application/javascript', // json数据高亮
                theme: 'paraiso-light', // 设置主题 记得引入对应主题才有显示   import 'codemirror/theme/blackboard.css'
                // tabSize: 4,
                lineNumbers: true, // 显示行号
                cursorHeight: 0.8, //   光标高度，默认是1
                autoCloseBrackets: true,
                matchBrackets: true, // 括号匹配
                // lineWrapping: 'wrap', // 文字过长时，是换行(wrap)还是滚动(scroll),默认是滚动
                showCursorWhenSelecting: true, // 文本选中时显示光标
                smartIndent: true, // 智能缩进
                completeSingle: false, // 当匹配只有一项的时候是否自动补全
                // readOnly:true,
                extraKeys: {
                        "F2": async function(cm) {
                            //alert(JSON.stringify(This.element.code))
                            This.run()
                        },
                        "F10": function(cm) {
                            let fontSize = $(cm.getWrapperElement()).css('font-size')
                            let lineHeight = $(cm.getWrapperElement()).css('line-height')
                            console.log('cm-style:'+fontSize+','+lineHeight)
                             $(cm.getWrapperElement()).css('font-size',(parseInt(fontSize)+1)+'px')
                             $(cm.getWrapperElement()).css('line-height',('150%'))
                        },
                        "F9": function(cm) {
                            let fontSize = $(cm.getWrapperElement()).css('font-size')
                            // let lineHeight = $(cm.getWrapperElement()).css('line-height')
                            // lineHeight = parseInt(lineHeight)<=22 ? 22:lineHeight
                            console.log('cm-style:'+fontSize)
                             $(cm.getWrapperElement()).css('font-size',(parseInt(fontSize)-1)+'px')
                             $(cm.getWrapperElement()).css('line-height',('150%'))
                            //  $(cm.getWrapperElement()).css('line-height',(parseInt(lineHeight)-2)+'px')
                        },
                        "F8": function(cm) {
                             $(cm.getWrapperElement()).css('font-size',('13px'))
                             $(cm.getWrapperElement()).css('line-height',('150%'))
                        },
                        "F7": function(cm) {
                             $(cm.getWrapperElement()).css('font-size',('15px'))
                             $(cm.getWrapperElement()).css('line-height',('150%'))
                            //  $(cm.getWrapperElement()).css('line-height',('20px'))
                        },
                        "Esc": function(cm) {
                            if(This.dialogVisible)
                            {
                                return This.dialogVisible=false
                            }
                            var fullScreen = cm.getOption("fullScreen");
                            // cm.setOption("fullScreen", !fullScreen);
                            //console.log('Esc key down:'+fullScreen)
                            //2个，固定高度在侧栏，可全屏（但是按esc退出全屏后，侧栏工具栏按钮乱了次序和位置--从底部变成在editor顶部了）
                            //3个parent，不固定高度（随内容变化--已经有el-collapse-item可折叠状态最佳，并且在CanvasAttr可设定editor，多种状态下最佳），可全屏（在非全屏状态下下拉到底部--内容足够长的情况下，也会显示状态栏按钮---最佳）
                            //4个parent，固定高度在侧栏，全屏后带工具栏按钮（但是不能全屏）
                            var fs_p = $(cm.getWrapperElement()).parent().parent().parent()// .parent().parent().parent().parent().parent()

                            if ( cm._ic3Fullscreen == null) {
                                cm._ic3Fullscreen = false;
                                cm._ic3container = fs_p.parent();
                            }

                            if (!cm._ic3Fullscreen)
                            {
                                fs_p = fs_p.detach();
                                fs_p.addClass("CodeMirrorFullscreen");
                                // $(cm.getWrapperElement()).removeClass('editor0')
                                // $(cm.getWrapperElement()).addClass('editor')
                                fs_p.appendTo(".home");//.home //使用body将无滚动事件
                                cm.focus();
                                cm._ic3Fullscreen = true;
                            }
                            else
                            {
                                fs_p = fs_p.detach();
                                fs_p.removeClass("CodeMirrorFullscreen");
                                // $(cm.getWrapperElement()).removeClass('editor')
                                // $(cm.getWrapperElement()).addClass('editor0')
                                fs_p.appendTo(cm._ic3container);
                                cm.focus();
                                cm._ic3Fullscreen = false;
                            }
                        },
                "Ctrl-Q": "autocomplete"
                    },
            }
        },
        curComponent() {
            return this.$store.state.curComponent
        },
    },
    beforeUpdate() {
        //这个是角色codeMirror内容不同步的问题的（非常严重，model无法进行有效的数据绑定）
        // this.editor = !this.editor
        //console.log('beforeUpdate:' + this.value)
        // setTimeout(()=>{
        // if(this.oldComponent!=this.$store.state.curComponent)
        let val = this.coder.getValue()
        if(val != this.value ) {//一般情况下CodeMirror的Editor的change事件修改，
        // 导致的绑定this.value与Model传输过来的是完全一样的内容都一样，除非是切换不同的Shape组件（编辑状态）
            this.setCodeContent(this.value) //注意这里是this.value（由上层传入-仅当Shape切换时）----平时编辑实时保存内容不会重新Edit.setValue（因为这样会导致光标问题）
        }
    },
    // updated() {
    //     this.$nextTick(() => {
    //             this.$refs.mycode.focus()
    //         })
    // },
    mounted() {
        this.poplang = new PopRuntime(this.element) //仅用于模拟执行。
        //this.element.poplang = this.poplang //这里会导致渲染崩溃（因为CanvasAttr里的属性会重复渲染--没有区分$poplang变量。
        const protocolCache = localStorage.getItem('protocol_config')
        // console.log('protocolCache:' + JSON.stringify(protocolCache))

        if (protocolCache) {
            this.poplang.setProtocol(JSON.parse(protocolCache))
            this.isok = 'ok'
        } else {
            this.isok = false
        }

        // console.log('mounted:' + this.value)
        // 初始化
        this.initialize()
        // if(window.fsm_config)
        // {
        //     let keywords = window.poplangKeywords
        //     window.fsm_config = protocol ? protocol.fsm_config:null
        //     console.log('fsm_config:'+JSON.stringify( fsm_config))
        //     for(var key in fsm_config ){
        //         console.log('keyword:'+fsm_config[key])
        //         keywords.push(fsm_config[key])
        //     }
        // } 
    },
    methods: {
        async run() {
            // alert('真实模拟')
            this.dialogVisible = true
            let This = this
            let iCnt = 0;
            let beginTime = new Date().getTime()
            This.popret = '运行中，耗时：'+(iCnt*10)+'ms'
            let x = setInterval(()=>{
                This.popret = '运行中，耗时：'+(new Date().getTime() - beginTime)+'ms'
                iCnt++
            },10)
            let code = this.element.code
            let poplang = this.poplang
            if(poplang)
            {
                
                let results = null;
                
                setTimeout( async ()=>{
                    results = await poplang.runScript({},code,true)
                    console.log('results:'+JSON.stringify(results))
                    results['$pop.lang执行行数'] = (results.results ?results.results.length:0 )+'行（收集到执行结果的行数）'
                    results['$pop.lang运行耗时'] = ((new Date().getTime() - beginTime))+'ms'
                    This.popret = results//'运行耗时：'+((new Date().getTime() - beginTime))+'ms'+"</br>运行结果：<br/>"+JSON.stringify(results)
                    clearInterval(x)
                },1)
                //alert(JSON.stringify(results))
                // this.dialogVisible = true
            }
            else {
                this.popret = '【错误信息】运行时未启动'
                clearInterval(x)
            }
                //alert('运行时未启动')
            
        },
        // 初始化
        initialize() {
            //this.oldComponent = this.$store.state.curComponent
        // 初始化编辑器实例，传入需要被实例化的文本域对象和默认配置
            this.coder = CodeMirror.fromTextArea(
                this.$refs.mycode,
                this.coderOptions,
            )
            this.coder.on('inputRead', () => {
                this.coder.showHint()
            })
            // 编辑器赋值
            if (this.value || this.code) {
                this.setCodeContent(this.value|| this.code)
            } else {
                this.coder.setValue('')
            }
            // 支持双向绑定
            this.coder.on('change', (coder) => {
                // console.log('coder-change:'+coder.getValue())
                this.code = coder.getValue()
                // this.oldComponent = this.$store.state.curComponent
                //调用时才使用---在CommonAttr.vue中已经设置
               // this.$store.state.curComponent.propValue.code = this.code
                // if (this.$emit) {
                //     this.$emit('input', this.code)
                // 直接在这里修改，因为上层的CommonAttr中的onchange事件没有生产（通过Vue的data-value：model绑定来影响内容的实时保存，仅当Shape组件切换时，才会导致beforeUpdate事件的有效调用--会让Editor.setValue--会改变光标位置）
                 //this.$store.state.curComponent.code = this.code
                 this.element.code = this.code
            })
        },
        setCodeContent(val) {
            //console.log('content-val:'+val)
            // setTimeout(() => {
                if (!val) {
                    this.coder.setValue('')
                } else {
                    this.coder.setValue(val)
                }
            // }, 3)
        },
        getCoder() {
            const that = this
            return new Promise((resolve) => {
                // ;
                let g = function get() {
                    if (that.coder) {
                        resolve(that.coder)
                    } else {
                        setTimeout(get, 10)
                    }
                }
                g()
            })
        },
        getLanguage(language) {
        // 在支持的语法类型列表中寻找传入的语法类型
            return this.modes.find((mode) => {
                // 所有的值都忽略大小写，方便比较
                const currentLanguage = language.toLowerCase()
                const currentLabel = mode.label.toLowerCase()
                const currentValue = mode.value.toLowerCase()
        
                // 由于真实值可能不规范，例如 java 的真实值是 x-java ，所以讲 value 和 label 同时和传入语法进行比较
                return (
                    currentLabel === currentLanguage || currentValue === currentLanguage
                )
            })
        },
        changeMode(val) {
        // 修改编辑器的语法配置
            this.coder.setOption('mode', `text/${val}`)
            // 获取修改后的语法
            const label = this.getLanguage(val).label.toLowerCase()
            // 允许父容器通过以下函数监听当前的语法值
            this.$emit('language-change', label)
        },
    },
}
</script>
<style>
.common-editor {
  width: 100%;
  height: calc(100% - 35px);
  /* font-size:15px; */
  line-height : 150%;
}
.CodeMirror {
    direction: ltr;
    line-height: 20px;
    width: 100%;
    height: 100%;
    float: left;
    border: 1px solid #f0f0f0;
}
.CodeMirror-fullscreen {
    display: block;
    position: fixed;
    top: 0; left: 0;
    width: 100%;
    height: 100%;
    z-index: 9999;
  }
  .CodeMirror-hints {
    z-index: 9999 !important;
  }
</style>